.searchInput {
    position: relative;
    width: 100%;
    background-color: #D8D8D8;
    height: 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    margin: auto;
}

.searchInput .iconContainer {
    position: absolute;
    left: 0px;
    width: 30px;
    height: 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.searchInput img {
    width: 20px;
}

.searchInput .inputContainer {
    width: 100%;
}

.searchInput input {
    width: 100%;
    height: 100%;
    background-color: #D8D8D8;
    border: none;
    outline: none;
    text-align: center;
    font-size: 14px;
    color: #8f8f8f;
    font-weight: 200;
}

.searchInput input::-webkit-input-placeholder {
    font-size: 12px;
    font-weight: 200;
    color: #8f8f8f;
}

.searchInput input::-moz-placeholder {
    font-size: 12px;
    font-weight: 200;
    color: #8f8f8f;
}


