.home {
    height: 100vh;
    overflow: auto;
}

.home .search {
    padding: 20px 20px 0px 20px;
}

.home .category {
    padding: 10px 20px;
}

.home .content {
    height: calc(100vh - 92px);
    overflow: auto;
    margin: 0px;
    padding: 0px;
}

.home .homeCarouselItem {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 200px;
}

.home .block {

}

.home .block .blockTitle {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 15px 0px 5px;
    font-size: 14px;
}

.home .block .blockTitle .title {
    font-size: 14px;
    font-weight: '700';
}

.home .block .blockTitle .subTitle {
    font-size: 11px;
    color: #8f8f8f;
    margin-top: 5px;
}

.home .block .blockTitle .subAction {
    font-size: 11px;
    color: #8f8f8f;
    margin-top: 5px;
}

.home .block .blockTitle .subAction label{
    margin-right: 5px;
}

.home .block .blockTitle .subAction img{
    width: 10px;
}

.home .block .newItems {

}

.home .block .newItemRow {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 0px 5px;
}

.home .block .newItemRow .item {
    margin: 3px;
}

.home .block .newItemRow .item img {
    width: 100%;
}

.home .block .scrolableRow {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    padding: 0px 5px;
    overflow: auto;
    -ms-flex-direction: row;
        flex-direction: row;
}

.home .block .scrolableRow .item {
    width: 100px;
    height: 100px;
}

.home .block .scrolableRow .item img {
    width: 100px;
    height: 100px;
}


