body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

a {
  text-decoration: none;
  color: #000;
}

label {
  margin-bottom: 0px;
  font-weight: 200;
}

input[type="password"] {
  font: caption;
  font-size: 24px;
  color: #8f8f8f;
}

.container {
  width: 100vw;
  height: 100vh;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  position: relative;
  overflow: auto;
  padding-right: 0px;
  padding-left: 0px;
}

.content {
  margin-top: 50px;
  padding-bottom: 50px;
}

.withoutHeader {
  margin-top: 0px;
}

.withoutFooter {
  padding-bottom: 0px;
}


