.onboarding {

}

.onboarding-slide {
    background-color: #171515;
    text-align: center;
    height: calc(100vh - 89px);
    overflow: hidden;
}

.onboarding-slide .logo {
    width: 70%;
    max-width: 300px;
    margin-top: 70px;
    margin-bottom: 70px;
}

.onboarding-slide .labels {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 130px;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    width: 70%;
    margin: auto;
}

.onboarding-slide .label {
    color: #fff;
}

.onboarding-slide .outline {
    font-size: 20px;
    margin-bottom: 40px;
}

.onboarding-slide .description {
    margin: 10px;
    font-size: 16px;
}

.onboarding-slide .demo {
    width: 70%;
    max-width: 300px;
}

.onboarding .indicator {
    height: 40px;
    background-color: #000;
}

.onboardingFooter {
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
}

.onboardingFooter .onboardingFooterContainer {
    height: 50px;
    background-color: #1a1a1a;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.onboardingFooter .item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: center;
        justify-content: center;
}
.onboardingFooter .item label {
    color: #fff;
}


