.cart {

}

.cart .brandItem {
    padding: 20px 10px 0px;
}

.cart .brandItem .brandTitle {
    font-size: 16px;
    line-height: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e3e2e1;
}

.orderItem {
    padding: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 1px solid #e3e2e1;
}

.orderItem .logo {
    margin-right: 10px;
}

.orderItem .logo img {
    width: 60px;
}

.orderItem .info {
    -ms-flex: 1 1;
        flex: 1 1;
    line-height: 20px;
}

.orderItem .info .productTitle {
    
}

.orderItem .info .productType {
    font-size: 12px;
    color: #8f8f8f;
}

.orderItem .quantity {
    font-size: 12px;
    color: #8f8f8f;
    padding: 5px;
    background-color: #e3e2e1;
    border-radius: 3px;
    text-align: center;
    width: 60px;
}

.orderItem .cost {
    font-size: 14px;
    padding: 5px 0px;
    width: 60px;
    text-align: right;
}

.globalShipping {
    text-align: center;
    font-size: 14px;
    color: #8f8f8f;
    border-bottom: 1px solid #e3e2e1;
    padding: 5px;
}

.totalCost {
    text-align: center;
    border-bottom: 1px solid #e3e2e1;
    padding: 5px;
    margin-bottom: 20px;
}

.cart .checkout {
    border-radius: 5px;
    background-color: #000;
    width: 100%;
    height: 100%;
    color: #fff;
    text-align: center;
    padding: 5px;
}

.cart .checkout a {
    display: block;
    width: 100%;
    height: 100%;
    color: #fff;
    text-align: center;
    padding: 5px;
}

/* remove padding of modal dialog*/
.checkout-modal {
    margin: 0px;
}

.checkout-modal .modal-content {
    border-radius: 0;
    background-color: rgba(0,0,0,0);
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}

/* remove padding of modal dialog*/
.editcart-modal {
    margin: 0px;
}

.editcart-modal .modal-content {
    border-radius: 0;
    background-color: rgba(0,0,0,0);
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}


