.productDetail {

}

.productDetail .logo {
    position: relative;
}

.productDetail .logo img {
    width: 100%;
}

.logo .forceGround {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: end;
        align-items: flex-end;
}
.logo .forceGround img{
    width: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
}

