        flex: 1 1;
    text-align: right;
    font-size: 14px;
    color: #8f8f8f;
}

.primaryInfo .left {
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: left;
}

.primaryInfo .brandName {
    font-size: 18px;
    line-height: 20px;
}

.primaryInfo .productName {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    font-size: 14px;
    color: #8f8f8f;
    line-height: 20px;
}

.primaryInfo .productName .productNameQuantity {
    background-color: #171515;
    font-size: 6px;
    width: 24px;
    height: 24px;
    border-radius: 12px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
