.secondaryInfo {
    font-size: 12px;
    color: #8f8f8f;
    line-height: 20px;
}

.productDetail .subInfo {
    
}

.productDetail .subInfo .item {
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    padding: 0px 10px;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 1px solid #e3e2e1;
    color: #171515;
    font-size: 14px;
}

.productDetail .subInfo .item img {
    /* width: 24px; */
    height: 16px;
}

.productDetail .review {
    padding: 10px 10px 0px 10px;
    font-size: 14px;
    border-bottom: 1px solid #e3e2e1;
    line-height: 20px;
}

.productDetail .review label{
}

.productDetail .review .totalResult {
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #e3e2e1;
    margin: 0px 10px;
    padding: 5px 0px;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.productDetail .review .totalResult .left {
    
}

.productDetail .review .totalResult .right {
    font-size: 12px;
}

.productDetail .review .totalResult .totalReviews {
    font-size: 12px;
    color: #8f8f8f;
    line-height: 20px;
}

.stars img {
    width: 14px;
    margin-right: 5px;
}

.productDetail .review .viewAllReviews {
    font-size: 12px;
    color: #8f8f8f;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 10px 0px;
}

.productDetail .question {
    padding: 10px 10px 0px 10px;
    font-size: 14px;
    border-bottom: 1px solid #e3e2e1;
}

.productDetail .question .inputContainer {
    margin: 10px 10px;
}

.productDetail .question .inputContainer input {
    width: 96%;
    height: 25px;
    border-radius: 3px;
    background-color: #e3e2e1;
    border: none;
    outline: none;
    text-align: left;
    font-size: 10px;
    color: #8f8f8f;
    padding: 0px 8px;
}

.productDetail .relatedProducts {
    font-size: 14px;
    text-align: center;
}

.productDetail .relatedProducts .title  {
    font-size: 14px;
    text-align: center;
    padding: 10px;
}





