.addresses {
    margin-top: 60px;
}

.addresses .addAddressButton {
    margin: 20px 20px;
    background-color: #333;
    border-radius: 10px;
    color: #fff;
    font-size: 16px;
    text-align: center;
    padding: 10px;
}

.addresses .addAddressButton label {
    color: #fff;
    font-size: 16px;
}

.edit-address-modal {
    margin: 0px;
    position: absolute;
    bottom: 0px;
}

.edit-address-modal .modal-content {
    border-radius: 0;
    background-color: rgba(0,0,0,0);
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
}

.edit-address-modal .modal-content .content {
    background-color: #fff;
    padding-bottom: 0px;
    margin-top: 0px;
}


