.refineBtn {
    margin: 10px auto;
    padding: 3px;
    background-color: rgba(0,0,0,0.7);
    color: #fff;
    width: 80px;
    text-align: center;
    border-radius: 5px;
    opacity: 0.6;
}

.refine-modal {
    margin: 0px;
}

.refine-modal .modal-content {
    border-radius: 0;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}


