.brand {
    
}

.brand .content {
    height: calc(100vh - 50px);
    overflow: auto;
    margin-top: 50px;
    padding: 0px;
}

.brand .brandItem {
    padding: 10px 10px;
    border-bottom: 1px solid #e3e2e1;
}

.brand .brandItem .brandInfo {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    padding: 5px 0px;
}

.brand .brandItem .brandInfo img {
    width: 40px;
    height: 40px;
    margin-right: 10px;
}

.brand .brandItem .brandInfo .brandTitle {
    font-weight: '800';
}

.brand .brandItem .brandInfo .brandDescription {
    font-size: 12px;
    color: #8f8f8f;
    line-height: 20px;
}

.brand .brandItem .products {
    overflow: auto;
    display: -ms-flexbox;
    display: flex;
    padding-top: 0;
}

.brand .products .product {
    margin-right: 10px;
    display: inline-block;
}

.brand .products .product img {
    width: 100px;
    height: 100px;
}


