.editcart {

}

.editcart .content {
    background-color: #fff;
    margin: 100px 30px;
    padding-bottom: 0px;
}

.editcart .editTitle {
    padding: 10px 20px;
    border-bottom: 1px solid #e3e2e1;
}

.editcart .remove {
    padding: 10px;
    text-align: center;
    color: red;
    border-bottom: 1px solid #e3e2e1;
}

.editcart .qtyItem {
    padding: 10px;
    position: relative;
    text-align: center;
    border-bottom: 1px solid #e3e2e1;
}

.editcart .qtyItem .cost {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    padding: 10px;
    font-size: 12px;
    color: #8f8f8f;
}

.editcart .cancel {
    padding: 15px;
    text-align: center;
    font-weight: 600;
}


