.forum {
    background-color: #fff;
    margin-top: 80px;
    padding-bottom: 50px;
    font-size: 14px;
}

.forumShortCutItem a{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
    border-bottom: 1px solid #e3e2e1;
}

.forumShortCutItem img {
    width: 10px;
}

.allTopics {
    border-top: 1px solid #e3e2e1;
}

.favoriteTopics {

}

.newestTopics {
    margin-top: 30px;
}

.newestTopics .title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
    border-bottom: 1px solid #e3e2e1;
}

.newestTopics .topics {
    padding: 0px 10px;
}


.new-topic-modal {
    margin: 0px;
}

.new-topic-modal .modal-content {
    border-radius: 0;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    background-color: rgba(0,0,0,0.6);
}


