.brandDetail {

}

.brandDetail .products {
    padding-top: 10px;
}

.brandDetail .content {
    margin: 0;
    padding: 0;
}

.brandDetail .brandDetailInfo {
    position: relative;
    height: 200px;
}

.brandDetail .brandDetailInfo .background img{
    width: 100%;
    height: 200px;
}

.brandDetail .brandDetailInfo .forceground {
    height: 200px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.3);
}

.brandDetailInfo label {
    color: #fff;
}


