.newTopic {
    padding: 10px;
}

.newTopic .formInput {
    border-bottom: 1px solid #fff;
    padding: 10px;
}
.newTopic .formInput:last-child {
    border: none;
}

.newTopic .formInput input {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    text-align: left;
    font-size: 16px;
    color: #fff;
    background-color: rgba(0,0,0,0);
}

.newTopic .formInput textarea {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    text-align: left;
    font-size: 16px;
    color: #fff;
    background-color: rgba(0,0,0,0);
}


.imageInput {
    background: #000;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 10px;
}

.imageInput .selectedImage {
    margin-right: 10px;
}

.imageInput .selectedImage img {
    width: 60px;
    height: 60px;
}

.imageInput .selectIcon img {
    width: 60px;
    height: 60px;
}



